.page {
  max-width: 1024px;
  margin: 0 auto;
  padding: 12px;
}

.Container {
  display: flex;
  flex-direction: row;
  margin-top: 10%;
}

.LeftContainer {
  width: 33rem;
  height: 28rem;
}

.RightContainer {
  display: flex;
  flex-direction: column;
  font-style: normal;
  letter-spacing: -0.035em;
}

.RightContainer h1 {
  font-weight: 700;
  font-size: 64px;
  line-height: 95px;
  color: #333333;
  font-family: 'Space Mono' !important;
}

.RightContainer p {
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
  color: #4f4f4f;
  font-family: 'Space Mono' !important;
}

.RightContainer button {
  font-family: 'Space Mono' !important;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: -0.035em;

  width: 13rem;
  height: 4.25rem;

  background-color: #333333;
  color: #ffffff;

  margin-top: 50px;

}

@media (max-width: 650px) {
  .Container {
    flex-direction: column;
    justify-content: center;
  }

  .LeftContainer {
    width: 80%;
    height: 80%;
    margin-left: 10%;
  }

  .RightContainer h1 {
    font-size: 48px;
    line-height: 1;
    margin-top: 40px;
  }

  .RightContainer p {
    font-size: 18px;
    margin-top: 0px;
  }

  .RightContainer button {
    justify-content: center;
    margin-top: 30px;
  }
}

.header-title {
  font-family: 'Inconsolata' !important;
  font-size: 24px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: -0.08em;
  text-align: left;

  color: #333333;
}

.FooterText {
  font-family: 'Montserrat' !important;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #bdbdbd;

  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 20px;
  width: 100%;
}

.FooterLink,
a:link,
a:active,
a:hover,
a:visited {
  font-weight: 700;
  color: #bdbdbd;
}

